import React, { Component } from 'react'
import { Text, View, TouchableOpacity, TextInput } from 'react-native'
import { httpGet } from './http/httpHandler'
import styles from './styles'

export class Demo extends Component {

    constructor(props) {
        super(props)

        this.state = {
            data: [{ name: '123' }],
            fatched: false
        }
    }

    componentDidMount() {
        this.getDataFromApi()
    }


    getDataFromApi() {
        httpGet('connectionService/getConnections').then(
            (result) => {
                if (result) {
                    return this.setState({
                        data: result,
                        fatched: true
                    });
                }

            },
        )
    }

    render() {
        const { input, container, headerText, loginButton } = styles
        if (this.state.fatched) {
            return (
                <View style={container}>
                    {/* <Text> Demo from Shared folder</Text>
                    <Text>Received Data: {this.state.data[0].name}</Text> */}
                    <Text style={headerText}>DevNet</Text>

                    <TextInput style={input} placeholder="email" onChangeText={text => this.setState({ email: text })} keyboardType="email-address" />
                    <TextInput style={input} placeholder="Password" secureTextEntry={true} onChangeText={text => this.setState({ password: text })} />

                    <TouchableOpacity onPress={() => this.onLogin()}>
                        <View
                            style={loginButton}>
                            <Text style={{ color: 'white', fontSize: 20, fontWeight: '800' }}>
                                Login
                        </Text>
                        </View>
                    </TouchableOpacity>
                </View>
            )
        } else {
            return (
                <View>
                    <Text> Demo from Shared folder  </Text>
                    <Text>Received Data: NO DATA(network error)</Text>
                </View>
            )
        }

    }
}

export default Demo
